import React from "react"
import Workflow from 'flow-kit';
import { Input, Popover } from 'antd'
import ReactJson from 'react-json-view'

import "./flow-vis.css"

const { TextArea } = Input;
  
const DEFAULT_DATA = {
  nodes: [
      { name: 'A', props: {
          email: 'abc@x.com',
          address: 'abc street'
      }},
      { name: 'B' },
  ],
  edges: [
      { from: 'A', to: 'B' },
      { from: null, to: 'A' },
      { from: 'B', to: null },
  ]
};

const copyJson = (x)=>{
    return JSON.parse(JSON.stringify(x));
}

const removeProps = (j, removedKey) => {
    let ret = copyJson(j)
    for(let r of removedKey) {
        delete ret[r]
    }
    return ret
}

const renderNode = (v) => {
    switch(v.id) {
        case '___START___':
            return (
            <div className={"workflow-node node-start"} >
            {v.name}
            </div>
        );
        case '___END___':
            return (
            <div className={"workflow-node node-end"} >
            {v.name}
            </div>
        )
        default:
            return (
            <div className={"workflow-node node-task"} >
            {v.name}
                <div>
                <ReactJson 
                src={
                    removeProps(v, ['id', 'name'])
                }
                name={null}
                iconStyle={'square'}
                indentWidth={2}
                collapsed={true}
                collapseStringsAfterLength={true}
                groupArraysAfterLength={10}
                enableClipboard={false}
                displayObjectSize={false}
                displayDataTypes={false}
                />
                </div>
            </div>
        )
    }
}

const myPreprocess = (d)=>{
    let data = copyJson(d)

    for(let n of data.nodes) {
        if(!n.hasOwnProperty('id')) {
            n.id = n.name;
        }
    }
    
    let start = null, end = null;
    for(let e of data.edges) {
        if(e.from === null) {
            start = {
                id: '___START___',
                name: 'START',
            }
            e.from = start.id;
        }
        if(e.to === null) {
            end = {
                id: '___END___',
                name: 'END',
            }
            e.to = end.id;
        }
    }
    if(start) {
        data.nodes.push(start)
    }
    if(end) {
        data.nodes.push(end)
    }

    // remove dup edges
    let getKey = function(e) {
        return `${e.from}->${e.to}`;
    }
    let edgeSet = new Set();
    for(let e of data.edges) {
        if(!edgeSet.has(getKey(e))) {
            edgeSet.add(getKey(e)) 
        }
        else {
            data.remove(e)
        }
    }

    return data;
}

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { error: null, errorInfo: null };
  }
  
  componentDidCatch(error, errorInfo) {
    // Catch errors in any components below and re-render with error message
    this.setState({
      error: error,
      errorInfo: errorInfo
    })
    // You can also log error messages to an error reporting service here
  }
  
  render() {
      let {data} = this.props
      if(this.data !== data) {
          this.data = data;
          this.state.errorInfo = null
          this.state.errorInfo = null
      }
    
    if (this.state.errorInfo) {
      // Error path
      return (
          <div>
          <Workflow
          className={"workflow"}
          data={myPreprocess(DEFAULT_DATA)}
          renderNode={renderNode}
          options={
              {
                ltor: true,
                layerMargin: 40,
                vertexMargin: 40, 
                edgeMargin: 150
              }
          }
          />
❎
          <details style={{ whiteSpace: 'pre-wrap' }}>
            {this.state.error && this.state.error.toString()}
            <br />
            {this.state.errorInfo.componentStack}
          </details>
          </div>
      );
    }
    // Normally, just render children
    return this.props.children;
  }  
}

const FlowVis = (props) => {
    const [data, setData] = React.useState(myPreprocess(DEFAULT_DATA));
    
  return (<div>
      <TextArea onChange={(t)=>{
          try {
              t = t.target.value
              t = t.replace(/'/g, '"')
              t = t.replace(/(['"])?([a-z0-9A-Z_]+)(['"])?:/g, '"$2": ')
              t = JSON.parse(t)
              setData(myPreprocess(t))
          }
          catch(e) {
              setData(myPreprocess(DEFAULT_DATA))
          }
      }} 
      autoSize={{minRows:10}}
      defaultValue={(JSON.stringify(DEFAULT_DATA, null, 2))}
      />
      <ErrorBoundary
      data={data}
      >
      <Workflow
      className={"workflow"}
      data={data}
      renderNode={renderNode}
      options={
          {
          ltor: true,
          layerMargin: 40,
          vertexMargin: 40,
          edgeMargin: 150
          }
      }
      />
      </ErrorBoundary>
      </div>
  );
}


export default FlowVis

